.form-control {}

.formControl.error input,
.formControl.error textarea {
    border: 1px solid red;
}

.formControl span {
    display: none;
}

.formControl.error span {
    display: block;
}

.formSummaryError {
    border: 1px solid red;
    padding: 5px;
    color: rosybrown;
}